$colorWhite: #fff;
$colorBlack: #000;
$colorGrey: #666;
$colorLightGrey: #999;
$colorDarkGrey: #333;
$colorGreen: #4ab020;
$colorLightGreen: #99c71f;
$colorDarkGreen: #648300;
$colorRed: #b01e1e;
$colorLightRed: #ce7f7f;
$colorDarkRed: #881616;

html,
body {
	height: 100%;
}

body {
	color: $colorBlack;
	font-size: 14px;
	font-family: Roboto, sans-serif;
	line-height: 1.25;
}

* {
	outline: none;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: 1em;
	line-height: 1;
	margin: 0;
}

img {
	height: 100%;
	width: 100%;
	object-fit: cover;
}

a {
	color: black;
	text-decoration: none;
	transition: opacity 0.4s ease-in-out;
	&:hover {
		opacity: 0.7;
		text-decoration: none;
	}
}

ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}

p {
	margin: 0;
}

button {
	outline: none;

	&:focus {
		outline: none;
	}
}

[class^="icon-"],
[class*=" icon-"] {
	position: relative;
	vertical-align: middle;
}

.land-wrapper {
	position: relative;
	min-width: 320px;
	min-height: 100%;
	overflow: hidden;
}


.land-container{
	margin: 0 auto;
	max-width: 1200px;
}
@media (max-width: 1250px) {
	.land-container{
		padding: 0 20px;
	}
}



.land-title_h1 {
	position: relative;
	z-index: 3;
	color: var(--Text-Inverse, #FFF);
	padding-bottom: 24px;


	font-family: 'Unbounded', sans-serif;
	font-size: 44px;
	font-style: normal;
	font-weight: 500;
	line-height: 110%;
}



.land-title_h2 {
	color: var(--Text-Primary, #262626);
	padding-bottom: 26px;

	font-family: 'Unbounded', sans-serif;
	font-size: 36px;
	font-style: normal;
	font-weight: 500;
	line-height: 110%; /* 39.6px */
}


@media (max-width: 768px){
	.land-title_h1 {
		font-size: 35px;
	}
	.land-title_h2{
		font-size: 25px;
		padding-top: 40px;
	}
}


@media (max-width: 480px) {
	.land-title_h1{
		font-size: 28px;
	}
	.land-title_h2{
		font-size: 22px;
		padding-top: 40px;
	}

}
